.main_block {
    background: grey;
}

.content_block {
    display: grid;
    width: 90%;
    justify-self: center;
    align-items: center;
    margin: 0 auto;
    grid-template-columns: repeat(2, 5fr) 2fr;
}

.content_block div {
    display: grid;
    border-radius: 5px;
    justify-items: center;
    align-items: center;
    height: 4rem;
}

.content_block input {
    padding: 10px;
    width: 60%;
};

.search_input {
    padding: 5px 10px;
}

.add_operator_button {
    background-color: #FBBF24;
    width: 100%;
    font-weight: bold;
    border-radius: 10px;
    transition: 0.5s;
    padding: 5px 5px;
}

.add_operator_button:hover {
    background-color: #FF9824;
}