.modal {
    display: block;
    z-index: 20;
}

.absolute_name {
    display: grid;
    justify-items: center;
    font-size: 50px;
}

.translations_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
}

.translations_block p {
    font-size: 20px;
    font-weight: bold;
    margin: 10px;
}

.active_specifications_block, .deleted_specifications_block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 50px;
}