.main_block { 
    display: grid;
}

.specifications_block {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    justify-items: center;
    align-items: center;
    text-align: center;
    width: 100%;
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 2px solid #000;
    background-color: #fff;
}

.edit_button {
    display: block;
    background-color: blue;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    padding: 10px 15px;
    border-radius: 10px;
    transition: 0.5s;
}

.edit_button:hover {
    background-color: #280080;
}

.packman_loader {
    display: grid;
    justify-self: center;
    align-self: center;
    margin-top: -50px;
    background-color: #F0EDE5;
}