.main_block {
    position: sticky;
    display: grid;
    top: 0;
    left: 0;
    display: inline-block;
    height: 100vh;
    padding: 0 10px;
    margin-right: 40px;
    background-color: #fff;
}

.links_initial {
    border-radius: 5px;
    width: 100%;
    display: grid;
    font-size: 20px;
    margin: 20px 0;
    justify-items: start;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    font-weight: bold;
    transition: 1s;
}

.links_initial:hover {
    background-color: #FBBF24;
}

.logout {
    cursor: pointer;
}