.email_block {
    display: grid;
    align-self: center;
    width: 100%;
}

.email_block label {
    display: block;
    font-size: 1.2rem;
    font-weight: bold;
}

.email_block input {
    position: relative;
    display: block;
    font-size: 17px;
    border: none;
    width: 95%;
    padding: 10px 0 10px 10px;
    border-radius: 5px;
    background-size: 20px 20px;
    background-color: #E4E1DF;
}

.error_message {
    color: red;
    font-size: 16px;
}