.main_block {
    display: grid;
}

.content_block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 50px;
    width: 90%;
    align-items: start;
}

.content_block div {
    margin: 20px;
    display: grid;
    justify-items: center;
    align-items: center;
    width: 5rem;
    cursor: pointer;
    height: 5rem;
    background-color: grey;
    text-align: center;
} 

.main_location {
    background-color: #fff;
    margin-top: 50px;
}

.packman_loader {
    display: grid;
    justify-self: center;
    align-self: center;
    margin-top: -50px;
    background-color: #F0EDE5;
}