.content_block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    align-items: center;
}

.content_block div {
    width: 5rem;
    height: 5rem;
    margin: 10px;
    background-color: gray;
}

.add_region_button {
    padding: 10px 15px;
    background-color: orange;
}