.main_block {
    display: grid;
}

.content_block {
    display: inline-block;
    background-color: #fff;
    width: 100%;
    height: 200vh;
    padding: 20px 30px;
}


.operators_block {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-items: center;
    align-items: center;
    text-align: center;
    width: 100%;
    margin-top: 20px;
    border-bottom: 2px solid #000;
    background-color: #fff;
}

.operators_block div {
    margin: 30px 0;
}

.edit_button, .delete_button {
    background-color: blue;
    padding: 10px 10px;
    color: #fff;
    border-radius: 10px;
    transition: 0.5s;
    z-index: 1;
}

.delete_button {
    background-color: red;
}

.edit_button {
    background-color: blue;
}

.delete_button:hover, .edit_button:hover {
    transform: scale(1.1);
}

.button {
    border: 2px solid #000;
    cursor: pointer;
}

.pages {
    margin: 20px 0;
}

.page {
    bottom: 1px solid lightgray;
    border-radius: 100%;
    padding: 5px 10px;
    margin: 0 10px;
    cursor: pointer;
}

.packman_loader {
    display: grid;
    justify-self: center;
    align-self: center;
    margin-top: -50px;
    background-color: #F0EDE5;
}