.form_block input {
    display: grid;
    justify-self: center;
    border: 1px #000 dashed;
    margin: 40px;
}

.add_main_location_button {
    display: grid;
    justify-self: center;
    color: #fff;
    font-weight: bold;
    padding: 10px 15px;
    border-radius: 10px;
    background-color: blue;
    margin: 10px;
}