.main_block {
    display: grid;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: transparent;
}

.content_block {
    display: grid;
    justify-self: center;
    background-color: gray;
    padding: 10px;
    align-self: center;
}

.absolute_name_block {
    display: grid;
    grid-template-columns: 1fr;
}

.add_absolute_value_button {
    padding: 10px 20px;
    color: #fff;
    border-radius: 10px;
    background-color: green;
}


.remove_absolute_value_button {
    background-color: red;
    color: #fff;
    border-radius: 10px;
    height: 40px;
}

.content_block input, .content_block button {
    margin: 10px;
}

.add_absolute_value_button {
    padding: 10px 20px;
    color: #fff;
    border-radius: 10px;
    background-color: green;
}

.absolute_name_checkbox_block {
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

.absolute_name_checkbox_block label {
    font-size: 18px;
}

.absolute_name_translation {
    display: grid;
    grid-template-columns: 2fr 2fr;
}

.absolute_name_checkbox_block input {
    cursor: pointer;
    width: 16px;
    height: 14px;
} 

.add_absolute_value_block {
    display: grid;
    grid-template-columns: 2fr 1fr;
}

.create_button {
    display: grid;
    justify-self: center;
    background-color: blue;
    padding: 10px;
    font-size: 18px;
    border-radius: 10px;
    color: #fff;
    font-size: bold;
}

.create_button:hover {
    background-color: darkblue;
}