.main_block {
    background: grey;
}

.content_block {
    display: grid;
    width: 90%;
    justify-self: center;
    align-items: center;
    padding: 20px 0;
    margin: 0 auto;
    grid-template-columns: 2fr 2fr;
}

input {
    border-radius: 3px;
    padding: 10px;
}

.add_specification_button {
    background-color: blue;
    margin: 0 auto;
    width: 50%;
    font-weight: bold;
    font-size: 18px;
    color: #fff;
    padding: 10px 20px;
    transition: 0.5s;
}

.add_specification_button:hover {
    background-color: darkblue;
}