.modal {
    display: grid;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background: transparent;
}

.content_block {
    display: grid;
    justify-self: center;
    align-self: center;
    width: 100%;
    background-color: gray;
    width: 20rem;
    height: 20rem;
}

form {
    display: grid;
    align-items: center;
}

form div {
    display: grid;
    justify-items: center;
    align-items: center;
    width: 100%;
}

form label {
    font-size: 20px;
    color: #fff;
    display: block;
}

form input {
    width: 90%;
    border-radius: 5px;
    padding-left: 10px;

}

.save_changes_button {
    background-color: yellow;
    justify-self: center;
    width: 90%;
    padding: 10px 20px;
}

.error_message {
    color: red;
}