.main_block {
    width: 100vw;
    height: 100vh;
    display: grid;
    background-color: transparent;
    justify-items: center;
    align-items: center;
}

.content_block {
    display: grid;
    width: 20rem;
    height: 23rem;
    background-color: #fff;
}

.title {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}

form {
    padding: 0;
    margin: 0;

}

form div {
    display: grid;
    justify-items: center;
    align-items: center;
}

.add_button {
    background-color: #FBBF24;
    width: 80%;
    transition: 0.5s;
    border-radius: 20px;
    margin: 0 auto;
    padding: 5px 15px;
    font-size: 18px;
    font-weight: bold;
}

.add_button:hover {
    background-color: #FF9824;
}