.content_block {
    margin-top: 20px;
}

.items_block {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: repeat(7, 1fr);
    margin-top: 20px;
}

.items_block div {
    width: 100%;
    text-align: center;
    border-right: 2px solid #000;
}

.edit_button {
    background-color: blue;
    color: #fff;
    font-weight: bold;
    margin: 0 20px;
    transition: 0.5s;
}

.edit_button:hover {
    background-color: darkblue;
}