.main_block {
    display: grid;
    justify-items: center;
    align-items: center;
    width: 100vw;
}

.content_block {
    width: 100vw;
    height: 100vh;
    background-color: gray;
}

.x_image_block {
    cursor:pointer
}

.x_image_block {
    cursor: pointer;
    position: absolute;
    z-index: 1;
    top: 5px;
    right: 5px;
    width: 20px;
    height: 20px;
    transition: 0.5s;
}

.x_image_block:hover {
    transform: rotate(360deg);
}

.swith_block {
    display: inline-block;
    width: 50px;
    height: 100px;
}

.switch {
    display: grid;
    justify-items: start;
    align-items: center;
    vertical-align: middle;
    width: 100%;
    padding-left: 0;
}

.loader_image {
    width: 50px;
    height: 50px;
}