.main_block {
    display: grid;
}

.content_block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    align-items: start;
}

.content_block div {
    margin: 20px;
    padding: 20px;
    background-color: gray;
    justify-items: center;
    cursor: pointer;
}